/** @jsx jsx */
import { jsx } from 'theme-ui'
import { useState, useEffect } from 'react'
import {
  CircularProgressbarWithChildren,
  buildStyles,
} from 'react-circular-progressbar'
import 'react-circular-progressbar/dist/styles.css'
import { CKIcon, KwicrIcon } from '../Icons'
import { motion } from 'framer-motion'
import theme from '../../styles/theme'
import AvgLogo from '../AvgLogo';

const slides = [
  {
    client: 'Credit Karma',
    icon: <CKIcon />,
    text: `“Contractors on paper, employees in real life. This is what makes
            Bitmatica special and unique compared to any other set of
            contractors I've worked with. It's not about the money. It's about
            top notch ownership driven by a deep passion to build high quality
            software.”`,
    color: '#3DDB93',
  },
  {
    client: 'AVG',
    icon: <AvgLogo />,
    text: `"Bitmatica is quick to understand new systems, eager to deliver quality results with high impact, and has a deep technical background."`,
    color: 'white',
    padding: '0px'
  },
  {
    client: 'Kwicr',
    icon: <KwicrIcon />,
    text: `"Bitmatica quickly understood the situation we were in and educated and guided us through the product and technical designs that needed to be made."`,
    color: 'white',
  },
]
const INTERVAL_TIME = 5000
const INITIAL_INDEX = 0

const QuoteCarousel = ({ inView }) => {
  const [index, setIndex] = useState(INITIAL_INDEX)
  const [animationPaused, setPauseAnimation] = useState(false)
  useEffect(() => {
    const interval = setInterval(() => {
      console.log(animationPaused)
      if (!animationPaused) {
        if (index === slides.length - 1) {
            setIndex(INITIAL_INDEX)
        } else {
          setIndex(index + 1)
        }
      }
    }, INTERVAL_TIME)
    return () => clearInterval(interval)
  }, [index])

  const variants = {
    visible: {
      transition: {
        delay: 1,
        staggerChildren: 0.1,
      },
    },
    hidden: {
      transition: {
        staggerChildren: 0.05,
      },
    },
  }
  // console.log({ inView })
  return (
    <div
      sx={{
        display: 'flex',
        height: 'calc(100% - 2rem)',
        alignItems: 'center',
        flexDirection: 'column'
      }}
    >
      <motion.div
        initial={'hidden'}
        animate={inView && 'visible'}
        variants={variants}
        transition={{ delay: 1 }}
        sx={{ display: 'flex', width: '100%', my: '1rem' }}
      >
        {slides.map((slide, i) => (
          <motion.div
            variants={{
              visible: {
                y: 0,
                opacity: 1,
              },
              hidden: {
                y: 50,
                opacity: 0,
              },
            }}
            whileHover={index !== i && { scale: 1.1 }}
            whileTap={index !== i && { scale: 0.9 }}
            onClick={() => {
              setPauseAnimation(true)
              setIndex(i)
            }}
            sx={{
              width: '4rem',
              mr: '2rem',
              cursor: 'pointer',
              ':not(:last-child)': { mb: '1rem' },
            }}
          >
            <CircularProgressbarWithChildren
              value={i === index ? 100 : 0}
              strokeWidth={4}
              styles={buildStyles({
                pathColor: theme.colors.primary,
                trailColor: 'transparent',
                pathTransition:
                  i === index && !animationPaused
                    ? `stroke-dashoffset ${INTERVAL_TIME}ms ease-out 0s`
                    : 'none',
              })}
            >
              <div
                sx={{
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  borderRadius: '50%',
                  fontSize: '1.5rem',
                  backgroundColor: slide.color,
                  color: i === index ? 'white' : 'primary',
                  transition: 'all .3s',
                  width: '3rem',
                  height: '3rem',
                  padding: slide.padding || '.7rem',
                }}
              >
                {slide.icon}
              </div>
            </CircularProgressbarWithChildren>
          </motion.div>
        ))}
      </motion.div>
      <div>
        {slides.map((slide, i) => (
          index === i && (
          <motion.div initial={{ opacity: 0 }} animate={index === i ? { opacity: 1 } : { opacity: 0 }}>
            <div sx={{ fontSize: '1.1rem', mb: '2rem', color: 'text' }}>{slide.text}</div>
            <div sx={{ fontWeight: 600 }}>– {slide.client}</div>
          </motion.div>
          )
        ))}
      </div>

      {/* <div>{slides.map((slide, i) => index === i && slide.text)}</div> */}
    </div>
  )
}

export default QuoteCarousel
