import React from 'react'
import { StaticQuery, graphql } from 'gatsby'
import Img from 'gatsby-image'

const AvgLogo = () => (
  <StaticQuery
    query={graphql`
      query {
        file: file(relativePath: { eq: "logos/logo-avg.png" }) {
          childImageSharp {
            fixed(width: 40) {
              base64
              aspectRatio
              width
              height
              src
              srcSet
            }
          }
        }
      }
    `}
    render={data => <Img loading="lazy" fixed={data.file.childImageSharp.fixed} />}
  />
)

export default AvgLogo
