/** @jsx jsx */
import { jsx } from 'theme-ui'
import { motion } from 'framer-motion';

import SmoothScroll from 'smooth-scroll'

let scroll = null
if (typeof window !== `undefined`) {
  scroll = new SmoothScroll()
}

const variants = {
  visible: {
    y: 0,
    opacity: 1,
    transition: {
      y: { stiffness: 1000, velocity: -100 }
    }
  },
  hidden: {
    y: 50,
    opacity: 0,
  }
}


const ServiceCell = ({ icon, title, fill = 'primary', id, styles }) => (
  <motion.button
    onClick={() => {
      scroll.animateScroll(document.getElementById(id), null, {
        speed: 300,
      })
    }}
    variants={variants}
    transition={{ duration: 2 }}
    sx={{
      display: 'flex',
      p: '1rem',
      bg: 'secondaryBackground',
      color: 'white',
      width: '100%',
      borderRadius: '8px',
      cursor: 'pointer',
      transition: 'all .3s',
      ':hover': {
        bg: '#0e1015',
      },
      ...styles,
    }}
  >
    <div
      sx={{
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        width: '100%',
      }}
    >
      <div sx={{ display: 'flex', alignItems: 'center' }}>
        <div sx={{ display: 'flex', mr: '1rem', color: fill }}>{icon}</div>
        <div sx={{ fontWeight: 600 }}>{title}</div>
      </div>
      <div sx={{ opacity: 0.2 }}>→</div>
    </div>
  </motion.button>
)

export default ServiceCell;