/** @jsx jsx */
import { jsx } from 'theme-ui';
import { motion } from 'framer-motion'
import {
  ReactIcon,
  SwiftIcon,
  GraphqlIcon,
  ScalaIcon,
  CodeIcon,
} from '../../Icons';

const FeatureIcon = ({ styles, children }) => (
  <motion.div
    variants={{
      visible: {
        y: 0,
        opacity: 1,
        transition: {
          y: { stiffness: 1000, velocity: -100 },
        },
      },
      hidden: {
        y: 50,
        opacity: 0,
      },
    }}
    sx={{
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      bg: '#FFFFFF',
      alignSelf: 'center',
      width: 'fit-content',
      p: '1rem',
      borderRadius: '50%',
      boxShadow: '0px 17px 20px rgba(0, 0, 0, 0.11)',
      ...styles,
    }}
  >
    {children}
  </motion.div>
)

const DevFeature = ({ inView, styles }) => {
  const variants = {
    visible: {
      transition: {
        staggerChildren: 0.07,
      },
    },
    hidden: {
      transition: {
        staggerChildren: 0.05,
      },
    },
  }
  return (
    <motion.div
      initial={'hidden'}
      animate={inView && 'visible'}
      variants={variants}
      sx={{ position: 'relative', width: ['100%', 'fit-content', 'fit-content'], ...styles }}
    >
      <div sx={{ display: 'flex', width: '100%' }}>
        <CodeIcon />
      </div>
      <FeatureIcon
        styles={{ position: 'absolute', top: '-3rem', left: '-1rem' }}
      >
        <SwiftIcon size={64} />
      </FeatureIcon>
      <FeatureIcon
        styles={{ position: 'absolute', bottom: '-1rem', left: '5rem' }}
      >
        <ScalaIcon />
      </FeatureIcon>
      <FeatureIcon
        styles={{ position: 'absolute', top: '-1.5rem', left: '9rem' }}
      >
        <GraphqlIcon />
      </FeatureIcon>
      <FeatureIcon
        styles={{ position: 'absolute', top: '3rem', right: '1rem' }}
      >
        <ReactIcon size={64} />
      </FeatureIcon>
    </motion.div>
  )
}

export default DevFeature
