import React from 'react'
import { StaticQuery, graphql } from 'gatsby'
import Img from 'gatsby-image'

const LinkedInLogo = () => (
    <StaticQuery
      query={graphql`
        query {
          file: file(relativePath: { eq: "logos/logo-linkedin.png" }) {
            childImageSharp {
              fixed(width: 20) {
                base64
                aspectRatio
                width
                height
                src
                srcSet
              }
            }
          }
        }
      `}
      render={data => <Img loading="lazy" fixed={data.file.childImageSharp.fixed} />}
    />
  )
  export default LinkedInLogo
