/** @jsx jsx */
import { jsx } from 'theme-ui';
import SmoothScroll from 'smooth-scroll'
import { BitmaticaLogo } from "../Icons";

let scroll = null
if (typeof window !== `undefined`) {
  scroll = new SmoothScroll()
}

const Nav = () => (
  <div
    sx={{
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
      mt: '2rem'
    }}
  >
    <div
      sx={{
        display: 'flex',
        alignItems: 'center',
        width: '7rem',
        color: 'primary',
      }}
    >
      <BitmaticaLogo />
    </div>
    <nav>
      <button
        onClick={() => {
          scroll.animateScroll(document.getElementById("contact-form"), null, {
            speed: 300,
          })
        }}
        sx={{ bg: "transparent", color: "white", cursor: "pointer" }}
      >
        Contact
      </button>
    </nav>
  </div>
)

export default Nav;
