/** @jsx jsx */
import { jsx } from 'theme-ui'
import { StaticQuery } from 'gatsby';
import Img from 'gatsby-image'

export const Avatar = ({ size = 30 }) => (
    <div
        sx={{
            width: size,
            height: size,
            borderRadius: "100%",
        }}>
        {/* <img sx={{ borderRadius: "100%" }} src={src} alt="avatar" /> */}
        <StaticQuery
            query={graphql`
                query {
                    file: file(relativePath: { eq: "face.jpeg" }) {
                        childImageSharp {
                            fluid(maxWidth: 30) {
                                base64
                                aspectRatio
                                src
                                srcSet
                                sizes
                            }
                        }
                    }
                }
            `}
            render={data => <Img loading="lazy" sx={{ borderRadius: "100%" }} fluid={data.file.childImageSharp.fluid} />}
        />
    </div>
);
